









import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import { routerModule, repositoriesModule } from "@/store/modules";
import Timeline from "@/components/Timeline.vue";

@Component({
  components: {
    Timeline
  }
})
export default class RepositoryLogsView extends Mixins(LoadingScreen) {
  get repoId(): string {
    return routerModule.params.repositoryId;
  }

  mounted() {
    this.setLoading(true);
    setTimeout(() => this.setLoading(false), 0);
  }
}
